import React from "react"
import SpokenLanguages from "./spokenLanguages"
import { Box, Tab, Tabs } from "@mui/material";
import WhoIAm from "./whoiam";
import TabPanel from "./tabPanel";
import Skills from "./skills";
import ScholarCourse from "./scholar_course";

interface ResumeState{
    value: number;
    contentArray: {
        title:String,
        render:JSX.Element
    }[]
}

interface ResumeProps{

}

class Resume extends React.Component<ResumeProps, ResumeState>{
    constructor(props:ResumeProps){
        super(props);
        this.state = {
            value: 0,
            contentArray: [
                {
                    title:"Petite Introduction",
                    render:<WhoIAm/>
                },
                {
                    title:"Langues parlées",
                    render:<SpokenLanguages/>
                },
                {
                    title:"Compétences",
                    render:<Skills/>
                },
                {
                    title:"Parcours réalisé",
                    render:<ScholarCourse/>
                },
            ]
        };
        this.handleState = this.handleState.bind(this);
    }
    handleState(event: React.SyntheticEvent, newValue:number){
        this.setState({
            value:newValue
        });
    }

    render(){
        const state = this.state;
        return(
            <div id = "Resume" style={{
                alignItems: 'center',
                flexWrap: 'wrap',
                width: "80",
                marginTop: "auto",
                position: "fixed",
                top: "12.5%",
                left: "30%",
                margin: 0,
                padding:"0px 20px 60px 0"
            }}>
                <Box sx={{ 
                    width: '100%'
                }}>
                    <Box>
                        <Tabs
                            value={state.value}
                            onChange={this.handleState}
                            aria-label="basic tabs example"
                            sx={{
                                borderBottom: 1,
                                borderColor: "divider"
                            }}
                        >
                            {state.contentArray.map((singleContent,i)=>{
                                return(
                                    <Tab
                                    key={i.toString()}
                                    label={singleContent.title}
                                    id={i.toString()}
                                    aria-controls={"panel-"+i.toString()}
                                    />
                                )
                            })}
                        </Tabs>
                    </Box>
                    {state.contentArray.map((singleContent, i)=>{
                        return(
                            <TabPanel
                                key={i.toString()}
                                value={state.value}
                                index={i}
                            >
                            {singleContent.render}
                            </TabPanel>
                        )
                    })}
                </Box>
            </div>
        )
    }
}

export default Resume;